const thorui = require('@/components/common/tui-clipboard/tui-clipboard.js');
import { mapActions, mapState } from 'vuex';
export default {
	data() {
		return {
			platform: 'uni-app',
			list: [],
			listOnline: [
				{
					id: 'basic',
					name: '基础组件',
					open: false,
					pages: [
						{
							name: '基础组件',
							page: '/pages/index/basic/basic'
						}
					]
				},
				{
					id: 'map',
					name: '地图',
					open: false,
					pages: [

						{
							name: '拖拽定位',
							page: '/pages/index/location/location'
						},
						{
							name: '周边兴趣点',
							page: '/pages/index/maps/maps'
						},

						{
							name: '地址解析',
							page: '/pages/index/longlat/longlat'
						},
						{
							name: '天气',
							page: '/pages/index/weather/weather'
						}
					]
				},
				{
					id: 'index',
					name: '索引列表',
					open: false,
					pages: [
						{
							name: '城市选择',
							page: '/pages/index/selectCity/selectCity'
						},
						{
							name: '索引列表',
							page: '/pages/index/indexList/indexList'
						},
						{
							name: '索引&吸顶效果',
							page: '/pages/template/chat/contacts/contacts'
						}
					]
				},
				{
					id: 'nav',
					name: '三级联动',
					open: false,
					pages: [

						{
							name: 'picker三级联动',
							page: '/pages/index/picker/picker'
						},

						{
							name: 'picker-view三级联动',
							page: '/pages/index/picker-view/picker-view'
						}
					]
				},
				{
					id: 'canvas',
					name: '二维码生成',
					open: false,
					pages: [
						{
							name: '二维码生成',
							page: '/pages/index/qrcode/qrcode'
						}
					]
				},
				{
					id: 'drawer',
					name: 'drawer抽屉',
					open: false,
					pages: [
						{
							name: 'drawer抽屉',
							page: '/pages/index/drawer/drawer'
						}
					]
				},
				{
					id: 'swipe',
					name: '滑动菜单',
					open: false,
					pages: [
						{
							name: '滑动菜单',
							page: '/pages/index/swipe-action/swipe-action'
						}
					]
				},
				{
					id: 'class',
					name: '分类菜单',
					open: false,
					pages: [
						{
							name: '顶部选项卡',
							page: '/pages/index/navbar/navbar'
						},
						{
							name: '垂直分类',
							page: '/pages/template/mall/classify/classify'
						},
						{
							name: '垂直分类联动',
							page: '/pages/index/classify/classify'
						}
					]
				},
				{
					id: 'refresh',
					name: '上拉加载下拉刷新',
					open: false,
					pages: [
						{
							name: '新闻列表',
							page: '/pages/index/news/news'
						},
						{
							name: '商品列表',
							page: '/pages/index/product/product'
						},





					]
				},
				{
					id: 'template',
					name: '模板',
					open: false,
					pages: [
						{
							name: '商城模板',
							page: '/pages/template/mall/mall/mall'
						},
						{
							name: '新闻模板',
							page: '/pages/template/news/index/index'
						},
						{
							name: '聊天模板',
							page: '/pages/template/chat/msgList/msgList'
						},
						{
							name: '登录模板',
							page: '/pages/template/login/login/login'
						}
					]
				}
			]
		};
	},
	computed: {
		...mapState(['networkConnected','isOnline'])
	},
	watch: {
		networkConnected(isConnected) {
			if(isConnected){
				this.menuHandle();
			}
		},
		isOnline(val){
			if(val){
				this.menuHandle();
			}
		}
	},
	onLoad() {

		this.platform = '小程序';

		this.$eventHub.$on('emit', data => {
			setTimeout(() => {
				this.tui.toast('您选择了：' + data);
			}, 350);
		});











		let interstitialAd = null;
		if (wx.createInterstitialAd) {
			interstitialAd = wx.createInterstitialAd({
				adUnitId: 'adunit-44bbe9a9087910e3'
			});
			interstitialAd.onLoad(() => {});
			interstitialAd.onError(err => {});
			interstitialAd.onClose(() => {});
		}
		if (interstitialAd) {
			interstitialAd.show().catch(err => {
				console.error(err);
			});
		}


		setTimeout(() => {
			this.menuHandle();
		}, 50);
	},
	methods: {
		...mapActions(['getOnlineStatus']),
		async menuHandle() {
			let isOnline = await this.getOnlineStatus();
			if (isOnline) {
				this.list = this.listOnline;
			} else {
				this.list = this.listOnline.splice(0, 7);
			}
		},
		kindToggle: function(e) {
			var id = e.currentTarget.id,
				list = this.list;
			for (var i = 0, len = list.length; i < len; ++i) {
				if (list[i].id == id) {
					list[i].open = !list[i].open;
				} else {
					list[i].open = false;
				}
			}
			this.list = list;
		},
		github: function() {
			const that = this;
			thorui.getClipboardData('https://github.com/dingyong0214/ThorUI-uniapp', res => {







			});
		},
		mall: function() {
			if (this.list.length < 8) return;
			uni.navigateTo({
				url: '../../template/mall/mall/mall'
			});
		},
		doc: function() {
			uni.navigateTo({
				url: '../../doc/doc/doc'
			});
		}
	}
};
